body {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
}
.input-button {
	position: absolute;
	top: -9999px;
	left: -9999px;
	visibility: hidden;
}
.box {
	@include transition(all, 1s, ease-in-out);
	box-shadow: 0px 0px 25px 5px transparent;
	background: url('/assets/img/container.png') repeat;
	opacity: 0.8;
	margin: 50px;
	padding: 50px;
	@include breakpoint($mob-p-sm){
		width: 100%;
		height: 100%;
		opacity: 0.4;
	}
	@include breakpoint($mob-l-sm){
		width: 100%;
		height: 100%;
		opacity: 0.4;
	}
	@include breakpoint($mob-p-md){
		border-radius: 1em;
		height: 500px;
		margin: 0 50px;
	}
	@include breakpoint($mob-l-md){
		border-radius: 1em;
		height: 500px;
		margin: 0 50px;
	}
	@include breakpoint($desktop-sm){
		border-radius: 1em;
		min-height: 500px;
		max-height: 80%;
	}
	@include breakpoint($desktop-md){
		border-radius: 1em;
		min-height: 500px;
	}
	@include breakpoint($desktop-lg){
		min-height: 500px;
		border-radius: 1em;
		max-height: 80%;
	}
	form {
		width: 100%;
		height: 100%;
		@include flex(column, center);
		@include breakpoint($mob-p-sm){
			@include flex(column, center);
		}
		@include breakpoint($mob-l-sm){
			@include flex(row, center);
		}
	}
	span.upload-icon {
		@include transition(all, 1s, ease-in-out);
		font-size: 20em;
		opacity: 0.8;
		margin: 25px;
		@include breakpoint($mob-p-sm){
			opacity: 1;
			font-size: 10em;
		}
		@include breakpoint($mob-p-md){
			font-size: 15em;
		}
		@include breakpoint($mob-l-sm){
			opacity: 1;
			font-size: 10em;
		}
		@include breakpoint($mob-l-md){
			font-size: 15em;
		}
	}
	h2.upload-label {
		@include transition();
		@include montserrat();
		margin: 25px;
		span.pc {
			display: none;
			@include breakpoint($desktop-sm){
				display: initial;
			}
			@include breakpoint($desktop-md){
				display: initial;
			}
			@include breakpoint($desktop-lg){
				display: initial;
			}
		}
		span.mob {
			display: initial;
			@include breakpoint($desktop-sm){
				display: none;
			}
			@include breakpoint($desktop-md){
				display: none;
			}
			@include breakpoint($desktop-lg){
				display: none;
			}
		}
		@include breakpoint($mob-p-sm){
			font-size: 1.5rem;
		}
		@include breakpoint($mob-l-md){
			font-size: 1.75rem;
		}
		@include breakpoint($mob-p-sm){
			font-size: 1.5rem;
		}
		@include breakpoint($mob-l-md){
			font-size: 1.75rem;
		}
		@include breakpoint($desktop-sm){
			font-size: 2rem;
		}
	}
	>* {
		pointer-events: none;
	}
	.preview {
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
		}
	}
	.preview,
	button {
		display: none;
	}
	span,
	h2,
	small {
		@include transition(transform, 0.3s, $takeoff);
	}
	&.hover-initial {
		transform: scale(1.1)!important;
	}
	&.hover,
	&:hover {
		opacity: 1;
		background: url('/assets/img/hover.png') repeat;
		transform: scale(1.01);
		box-shadow: 0px 0px 25px 5px $black-30;
		z-index: 999;
	}
	&:hover {
		cursor: pointer;
	}
	&.drop-valid {
		&:hover {
			z-index: initial;
			box-shadow: none;
			transform: none;
		}
		opacity: 1;
		span,
		h2,
		small {
			transform: translateY(-1500px);
			opacity: 0
		}
	}
}
.alert {
	opacity: 1!important;
}
